.titulo-boton {
  display: flex;
  align-items: center;
  margin: 2rem;
  flex-direction: row;
  justify-content: space-between;
}

.titulo-boton h2 {
  margin-right: 10px;
}

@media only screen and (max-width: 700px) {
  .titulo-boton {
    flex-direction: column;
  }

  .titulo-boton h2 {
    margin-right: 0px;
    margin-bottom: 10px;
  }
}

.curso-sortable {
  margin: 2rem;
}

.materia {
  min-height: 80px;
  border-color: #717171 !important;
  color: #eee !important;
  background-color: #262626 !important;
  border-left: 6px solid #ccc !important;
  margin-top: 16px !important;
  margin-bottom: 16px !important;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19) !important;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.materia-descripcion {
  width: 100%; /* Se cambió el ancho a 100% para adaptarse mejor al contenedor */
  padding: 8px 16px !important;
  float: left;
}

.materia-titulo {
  font-weight: 600;
  font-size: 15px;
}

.materia-iconos {
  display: flex;
  color: #ddd;
  flex-direction: row;
  gap: 3rem;
  margin-right: 1rem;
}

.materia-iconos-icono {
  margin-right: 1rem;
}

.icono-texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.icono-texto a {
  text-decoration: none;
  color: inherit;
}

.icono-texto i {
  font-size: 24px;
}

.icono-texto span {
  font-size: 14px;
  margin-top: 5px;
}

/* Animación de desvanecimiento */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Aplicar animación a las materias */
.materia {
  animation: fadeIn 0.5s ease-in-out;
}

@media only screen and (max-width: 600px) {
  .materia {
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .materia-titulo {
    margin-bottom: 2rem;
  }

  .materia-descripcion {
    position: relative;
  }

  .materia-iconos {
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 2rem;
  }

  .materia-iconos-icono {
    margin-right: 0;
    margin-bottom: 0.5rem;
  }
}
