.task-list {
  margin-top: 20px;
}

.task-item {
  background-color: #f9f9f9;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  list-style: none;
  transition: background-color 0.3s ease;
}

.task-ul {
  margin-right: 2rem;
  margin-left: 2rem;
}

.task-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 2rem;
}

.task-item:hover {
  background-color: #e0e0e0;
}

.task-link {
  color: #007bff;
  text-decoration: none;
}

.task-link:hover {
  text-decoration: underline;
}
