h1,
h2,
h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  text-align: center;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1e293b;
  margin-bottom: 5rem;
}

header {
  background: #fff;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;
}
header .logo a {
  text-decoration: none;
  display: inline-block;
  color: inherit;
  padding: 5px 0;
  margin: 0 10px;
  border: 3px solid transparent;
  text-transform: uppercase;
  transition: 0.2s;
  cursor: pointer;
}
header nav ul {
  display: flex;
}
header nav ul li a {
  display: inline-block;
  color: #000;
  padding: 5px 0;
  margin: 0 10px;
  border: 3px solid transparent;
  text-transform: uppercase;
  transition: 0.2s;
}
header nav ul li a:hover, header nav ul li a.active {
  border-bottom-color: dodgerblue;
}
header .hamburger {
  cursor: pointer;
  display: none;
}
header .hamburger div {
  width: 30px;
  height: 3px;
  margin: 5px 0;
  background: #000;
}
@media only screen and (max-width: 900px) {
  header {
    padding: 0 30px;
  }
}
@media only screen and (max-width: 700px) {
  header .hamburger {
    display: block;
  }
  header nav {
    position: absolute;
    width: 100%;
    left: -100%;
    top: 70px;
    background: #fff;
    padding: 30px;
    transition: 0.3s;
  }
  header nav ul {
    display: block;
  }
  header nav ul li a {
    margin: 5px 0;
  }
  header #nav_check:checked ~ nav {
    left: 0;
  }
}

.hr {
  border-color: dodgerblue;
}

.bloques {
  width: min(95%, 80rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bloques {
    margin-top: 3rem;
  }
}
.bloques__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 1450px) {
  .bloques__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 1030px) {
  .bloques__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(1, 1fr);
  }
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748b;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__image {
  margin-bottom: 2rem;
}
.formulario__main img {
  height: 20rem;
  width: 100%;
  object-fit: contain;
}
.formulario__sub {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin: 1rem 0;
}
.formulario__sub img {
  height: 5rem;
  width: 7rem;
  object-fit: contain;
  padding: 0.5rem;
  border: 1px solid #64748b;
  cursor: pointer;
  transition: 0.2s linear;
}
.formulario__sub img:hover {
  transform: scale(1.1);
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1e293b;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #ffffff;
  font-size: 2rem;
}
.formulario__label {
  margin-top: 1rem;
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  margin-top: 1rem;
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__checkbox {
  display: flex;
}
.formulario__checkbox {
  display: flex;
  align-items: center;
  margin-top: 2rem;
}
.formulario__checkbox input[type=checkbox] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #ccc;
  border-radius: 3px;
  margin-right: 10px;
  cursor: pointer;
}
.formulario__checkbox input[type=checkbox]:checked {
  border-color: dodgerblue;
  background-color: dodgerblue;
}
.formulario__checkbox label {
  font-size: 1.6rem;
  color: #333;
  cursor: pointer;
}
.formulario__radio {
  margin-left: 1rem;
  display: flex;
  margin-top: 1rem;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #41884f;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #ffffff;
}
.formulario__borde {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.formulario__submit {
  background-color: dodgerblue;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
  align-self: flex-start;
  margin: 0 auto;
  margin-top: 1rem;
}
.formulario__submit:hover {
  background-color: #0077ea;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}
.formulario__submit--azul {
  background-color: dodgerblue;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: 100%;
  align-self: flex-start;
}
.formulario__submit--azul:hover {
  background-color: #0077ea;
  cursor: pointer;
}
.formulario__submit--verde {
  background-color: dodgerblue;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: 100%;
  align-self: flex-start;
}
.formulario__submit--verde:hover {
  background-color: #0077ea;
  cursor: pointer;
}
.formulario__submit--rojo {
  background-color: dodgerblue;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: 100%;
  align-self: flex-start;
}
.formulario__submit--rojo:hover {
  background-color: #0077ea;
  cursor: pointer;
}

.acciones, .acciones--centrar {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.6rem;
  font-weight: 400;
}
.acciones__enlace--confirmar {
  background-color: dodgerblue;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.acciones__enlace--confirmar:hover {
  background-color: #0077ea;
  cursor: pointer;
}

.password-input-container {
  position: relative;
}

.password-input-container input {
  padding-right: 30px; /* Ajusta el espacio para el ícono */
}

.password-toggle {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  cursor: pointer;
}

.password-toggle i {
  font-size: 18px;
  color: #555;
  margin-bottom: 4rem;
}

.password-toggle:hover i {
  color: #333;
}

.titulo-boton {
  display: flex;
  align-items: center;
  margin: 2rem;
  flex-direction: row;
  justify-content: space-between;
}

.titulo-boton h2 {
  margin-right: 10px;
}

@media only screen and (max-width: 700px) {
  .titulo-boton {
    flex-direction: column;
  }
  .titulo-boton h2 {
    margin-right: 0px;
    margin-bottom: 10px;
  }
}
.curso-sortable {
  margin: 2rem;
}

.materia {
  min-height: 80px;
  border-color: #717171 !important;
  color: #eee !important;
  background-color: #262626 !important;
  border-left: 6px solid #ccc !important;
  margin-top: 16px !important;
  margin-bottom: 16px !important;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19) !important;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.materia-descripcion {
  width: 100%; /* Se cambió el ancho a 100% para adaptarse mejor al contenedor */
  padding: 8px 16px !important;
  float: left;
}

.materia-titulo {
  font-weight: 600;
  font-size: 15px;
}

.materia-iconos {
  display: flex;
  color: #ddd;
  flex-direction: row;
  gap: 3rem;
  margin-right: 1rem;
}

.materia-iconos-icono {
  margin-right: 1rem;
}

.icono-texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.icono-texto a {
  text-decoration: none;
  color: inherit;
}

.icono-texto i {
  font-size: 24px;
}

.icono-texto span {
  font-size: 14px;
  margin-top: 5px;
}

/* Animación de desvanecimiento */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Aplicar animación a las materias */
.materia {
  animation: fadeIn 0.5s ease-in-out;
}

@media only screen and (max-width: 600px) {
  .materia {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .materia-titulo {
    margin-bottom: 2rem;
  }
  .materia-descripcion {
    position: relative;
  }
  .materia-iconos {
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 2rem;
  }
  .materia-iconos-icono {
    margin-right: 0;
    margin-bottom: 0.5rem;
  }
}
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

table,
th,
td {
  border: 1px solid #333;
}

th,
td {
  padding: 10px;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #ddd;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.pagination a {
  display: inline-block;
  padding: 10px 20px;
  margin: 0 10px;
  background-color: #f2f2f2;
  text-decoration: none;
  color: #007df4;
  border-radius: 5px;
  font-size: 18px;
  transition: all 0.2s;
}

.pagination a:hover {
  background-color: #007df4;
  color: #ffffff;
  cursor: pointer;
}

.pagination a.active {
  background-color: #007df4;
  color: #ffffff;
}

.responsive {
  max-width: 100%;
  height: auto;
}

.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin: 0 auto;
  padding: 20px;
}

.student-list-container {
  max-height: 400px;
  overflow-y: auto;
}

.subject {
  border: 1px solid #ccc;
  padding: 20px;
}

.student-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.student-item label {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.student-item input[type=checkbox] {
  display: none;
}

.student-item input[type=checkbox] + span {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid #aaa;
  border-radius: 3px;
  background-color: #fff;
  margin-right: 10px;
}

.student-item input[type=checkbox]:checked + span::after {
  content: "✔";
  font-size: 16px;
  color: #555;
  position: absolute;
  top: 2px;
  left: 4px;
}

.student-item label span {
  margin-right: 10px;
}

@media only screen and (max-width: 600px) {
  .container {
    grid-template-columns: 1fr;
  }
}
.asistencia-englobador {
  margin: 2rem;
}

.asistencia-margenes {
  margin: 0 auto;
}

.asistencia-alumno {
  width: 90px;
  display: inline-block;
  margin: 0 6px 16px 0;
}

.asistencia-fecha {
  margin-bottom: 1rem;
}

.asistencia-cuadrado {
  color: #fff !important;
  padding-top: 4px !important;
  padding-bottom: 4px !important;
  border-radius: 5px !important;
  text-align: center !important;
}

.presente {
  background-color: #59a15b !important;
}

.ausente {
  background-color: #ea4a3e !important;
}

.task-list {
  margin-top: 20px;
}

.task-item {
  background-color: #f9f9f9;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  list-style: none;
  transition: background-color 0.3s ease;
}

.task-ul {
  margin-right: 2rem;
  margin-left: 2rem;
}

.task-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
